import { tooltipContainer } from "../inversify.config";
import { TOOLTIP_TYPES } from "../types";
const cardService = tooltipContainer.get(TOOLTIP_TYPES.CardService);
const browserSupportsTouch = () => navigator.maxTouchPoints > 0;
export const bindReferenceLink = (target, card) => {
    if (!browserSupportsTouch()) {
        target.href = cardService.getReferenceLink(card);
        target.target = "_blank";
    }
};
